.sfgov-event-card {
  @include rubik;

  background: $green-3;
  border: 0;
  border-radius: 8px;
  color: $white;
  display: block;
  padding: $rhythm * 2;
  text-decoration: none;

  .title {
    @include h5;

    color: $white;
    margin: 0 0 $rhythm 0;
  }

  .description {
    @include body-short;

    color: $white;
    margin: 0;
  }

  &.image,
  &.date {
    padding: 0;
    position: relative;

    .event-image,
    .event-date {
      border-radius: $radius $radius 0 0;
      height: 160px;
      padding-top: 26px;
      text-align: center;
    }

    .event-image {
      padding-top: 0;
      overflow: hidden;

      img {
        border-radius: $radius $radius 0 0;
        width: 100%;
      }
    }

    .event-date {
      color: $slate;
      background: $green-2;
      opacity: 0.65;
      display: none;

      @media screen and (min-width: $mq-narrow) {
        display: block;
        padding-top: px-to-rem(26);
      }

      .month {
        font-size: px-to-rem(32);
        line-height: px-to-rem(38);
      }

      .date {
        font-size: px-to-rem(64);
        line-height: px-to-rem(76);
      }
    }

    .event-info {
      padding: $rhythm * 2;
    }
  }

  &:hover {
    background: $green-4;
  }
}
