.sfgov-news-card {
  @include rubik;

  background: $yellow-3;
  border: 0;
  border-radius: $radius;
  color: $slate;
  display: block;
  padding: $rhythm * 2;
  text-decoration: none;

  .title {
    @include body-link-bold;

    color: $slate;
    margin: 0 0 $rhythm*6 0;
  }

  .description {
    @include body-short;

    color: $slate;
    margin: 0;
  }

  &:hover {
    background: $yellow-4;
  }
}
