.sfgov-topic-card {
  @include rubik;

  background: $grey-1;
  border: 0;
  border-radius: $radius;
  color: $slate;
  display: block;
  text-decoration: none;
  padding: $rhythm * 2;

  @media screen and (min-width: $mq-narrow) {
    padding: $rhythm * 4;
  }

  .title {
    @include h3;

    color: $bright-blue;
    margin: 0 0 $rhythm 0;
  }

  .description {
    @include body-short;

    color: $slate;
    margin: 0;
    opacity: 0.6;
  }

  &:hover {
    background: $grey-2;

    .description {
      opacity: 1;
    }
  }
}
